/* COMPONENTS BEGIN */

.logotype {
  font-size: 0;
  display: inline-block;
  cursor: pointer;

  // img, svg
  //   width: 157px
 }  //   height: 50px


.socials {
  display: flex;
  align-items: center;
  margin: 0 -10px;

  a {
    margin: 0 8px;
    padding: 0 5px;
    line-height: 0;
    color: $grey70;

    svg {
      path, circle {
        transition: all $transitionSpeed $ease; } }

    &:hover {
      svg {
        path, circle {
          fill: $black; } } }

    &.instagram {
      svg {
        width: 24px;
        height: 24px; } }

    &.fb {
      svg {
        width: 12px;
        height: 24px; } }

    &.vk {
      svg {
        width: 31px;
        height: 20px; } }

    &.youtube {
      svg {
        width: 28px;
        height: 21px; } }

    &.pinterest {
      svg {
        width: 23px;
        height: 29px; } }

    &.telegram {
      svg {
        width: 34px;
        height: 30px; } }

    &.whatsup {
      svg {
        width: 36px;
        height: 36px; } } } }

.menu {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;

  a {
    font-size: 14px;
    color: $black;
    text-transform: uppercase;
    font-weight: 400;
    margin: 0 25px;

    &:hover {
      color: $blue; } }

  .phone {
    display: none; } }


section {
  padding-top: 170px;

  &.no-padding-top {
    padding-top: 0; } }

