.image {
  display: inline-block;
  width: 100%;
  padding-bottom: 100%;
  overflow: hidden;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat; }

.slider-controls {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 20px;

  .swiper-pagination {
    position: static;
    min-width: 100px;
    text-align: center;
    width: auto;
    font-size: 20px;
    color: #000;
    letter-spacing: 2px; } }

.slider-arrow {
  width: 60px;
  height: 60px;
  border-radius: 6px;
  border: 1px solid $blue;
  transition: all $transitionSpeed $ease;
  cursor: pointer;
  position: relative;

  svg {
    position: absolute;
    top: 50%;
    left: 55%;
    transform: translate(-50%, -50%);
    width: 36px;
    height: 36px;

    path {
      transition: all $transitionSpeed $ease; } }

  &--prev {
    svg {
      left: 45%;
      transform: translate(-50%, -50%) rotate(180deg); } }

  &:hover {
    background: $blue;

    path {
      fill: $white; } } }

ul {
  margin: 0;
  padding: 0;

  li {
    margin: 0;
    padding: 0; }

  p {
    padding-left: 24px;
    position: relative;

    &:before {
      position: absolute;
      left: 0;
      top: 0;
      line-height: inherit;
      color: inherit;
      font-size: inherit;
      content: ''; }

    span {
      font-weight: 700; } }

  &.no-points {
    p {
      padding-left: 0;
      &:before {
        display: none; } } } }

@include r($wide) {
  section {
    padding-top: 110px;

    &.no-padding-top {
      padding-top: 0; } }

  .menu {

    a {
      margin: 0 15px; } } }

@include r($wide-s) {
  section {
    padding-top: 90px;

    &.no-padding-top {
      padding-top: 0; } }

  .fix-header {
    main {
      padding-top: 30px; } } }

@include r($desktop) {

  .fix-header {
    main {
      padding-top: 40px; } }

  section {
    padding-top: 60px;

    &.no-padding-top {
      padding-top: 0; } }

  ul {
    p {
 } } }      // padding-left: 40px

@include r($tablet) {
  .socials {

    a {
      font-size: 22px; } }

  section {
    padding-top: 35px;

    &.no-padding-top {
      padding-top: 0; } }

  .fix-header {
    main {
      padding-top: 60px; } }

  .menu {
    flex-direction: column;
    padding: 10px 0 0;

    a {
      padding: 12px 0;
      margin: 0;
      width: 100%;
      text-align: center;
      position: relative;

      &:after {
        position: absolute;
        left: 50%;
        transform: translateX(-50%);
        width: 15px;
        height: 1px;
        content: '';
        background: rgba($grey95, 0.4);
        top: 0; }

      &:first-child {
        &:after {
          display: none; } } } }

  .slider-arrow {
    width: 40px;
    height: 40px;
    border-radius: 6px;

    svg {
      width: 20px;
      height: 20px; } }

  .slider-controls {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 15px;

    .swiper-pagination {
      font-size: 14px;
      letter-spacing: 1px; } }

  ul {
    p {
 } } }      // padding-left: 30px

/* COMPONENTS END */
