.article {
  padding: 0;
  color: #7d7d7d;

  h2 {
    margin: 0; }

  p {
    font-size: 20px;
    line-height: 1.8; }

  &__w1000 {
    max-width: 1000px;
    width: 100%;
    margin-left: auto;
    margin-right: auto; }

  &__w1200 {
    max-width: 1200px;
    width: 100%;
    margin-left: auto;
    margin-right: auto; }

  .fs24 {
    font-size: 24px;
    line-height: 1.65; }

  .mt90 {
    margin-top: 90px; }

  .mt70 {
    margin-top: 70px; }

  .mt50 {
    margin-top: 50px; }

  &__gallery {
    margin-left: -20px;
    margin-right: -20px;
    width: calc(100% + 40px);
    font-size: 0;

    .swiper-slide {
      width: 33.333%;
      padding: 0 20px; }

    &-item {
      box-shadow: 0px 3px 10px 0px rgba(0, 0, 0, 0.11); } }

  ul {
    padding: 20px 0 0;

    li {
      padding: 50px 0 0; }

    p {
      padding-left: 55px; } }


  &__slider {
    font-size: 0;
    overflow: hidden;
    width: 100%;

    .swiper-slide {
      overflow: hidden; }

    .image {
      padding-bottom: 55%; } }

