@include r($desktop) {
  .article {

    p {
      font-size: 18px;
      line-height: 1.6; }

    .fs24 {
      font-size: 22px;
      line-height: 1.5; }

    .mt90 {
      margin-top: 70px; }

    .mt70 {
      margin-top: 50px; }

    .mt50 {
      margin-top: 35px; }

    &__gallery {
      margin-left: -10px;
      margin-right: -10px;
      width: calc(100% + 20px);

      .swiper-slide {
        padding: 0 10px; } }

    ul {
      padding: 15px 0 0;

      li {
        padding: 35px 0 0; }

      p {
        padding-left: 40px; } } } }

@include r($tablet) {
  .article {

    p {
      font-size: 14px;
      line-height: 1.5; }

    .fs24 {
      font-size: 16px;
      line-height: 1.4; }

    .mt90 {
      margin-top: 30px; }

    .mt70 {
      margin-top: 25px; }

    .mt50 {
      margin-top: 15px; }

    &__gallery {
      margin-left: -5px;
      margin-right: -5px;
      width: calc(100% + 10px);

      .swiper-slide {
        padding: 0 5px; } }

    ul {
      padding: 10px 0 0;

      li {
        padding: 15px 0 0; }

      p {
        padding-left: 30px; } }

    &__slider {
      width: calc(100% + 40px);
      margin-left: -20px;
      margin-right: -20px;

      .image {
        padding-bottom: 60%; } }

