/* SELECT2 BEGIN */

.select2 {
  width: 100% !important; }

.select2-container {
  box-sizing: border-box;
  display: inline-block;
  margin: 0;
  position: relative;
  vertical-align: middle;
  // width: 100% !important
  // max-width: 100%
  z-index: 99999;

  .select2-selection--single {
    box-sizing: border-box;
    cursor: pointer;
    display: block;
    user-select: none;
    -webkit-user-select: none;
    font-size: 0;
    line-height: 0;

    .select2-selection__rendered {
      display: block;
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
      width: 100%;
      border: none;
      border-bottom: 1px solid $fontColor;
      outline: none;
      border-radius: 0;
      background: none;
      padding: 20px 0;
      padding-right: 50px;
      font-size: 30px;
      font-family: $ago;
      text-transform: uppercase;
      line-height: 1.2;
      font-weight: 400;
      color: $fontColor;
      display: inline-block;
      transition: all $transitionSpeed $ease;
      -moz-appearance: none;
      -webkit-appearance: none; }

    .select2-selection__clear {
      position: relative; } }

  &[dir="rtl"] .select2-selection--single .select2-selection__rendered {
    padding-right: 8px;
    padding-left: 20px; }

  .select2-selection--multiple {
    box-sizing: border-box;
    cursor: pointer;
    user-select: none;
    -webkit-user-select: none;

    .select2-selection__rendered {
      display: block;
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
      width: 100%;
      font-size: 16px;
      padding: 9px 25px 15px 0;
      line-height: normal;
      border: none;
      border-bottom: 1px solid $white;
      border-radius: 0;
      background: none;
      transition: all $transitionSpeed $ease;
      user-select: none;
      color: $white;
      font-weight: 700;

      &:after {
        content: '';
        position: absolute;
        top: 50%;
        right: 0;
        transform: translateY(-50%);
        width: 12px;
        height: 6px;
        background: url(../img/dropdown-icon.svg) center center no-repeat;
        transition: all $transitionSpeed $ease;
        transform-origin: center center; } } }

  .select2-search--inline {
    float: left; }

  .select2-search__field {
    width: 100%;
    font-weight: 400;
    font-size: 15px;
    padding: 10px 0;
    line-height: normal;
    color: #000;
    border-radius: 0;
    background: none;
    border: none;
    border-bottom: 1px solid $white;
    color: $white;
    transition: none;
    resize: none;

    // display: none

    &::placeholder {
      color: rgba($white, 0.5);
      font-weight: 400;
      opacity: 1;
      display: block; }

    &::-webkit-search-cancel-button {
      -webkit-appearance: none; } } }

.select2-dropdown {
  background: #fff;
  border-radius: 0;
  overflow: hidden;
  border: none;
  box-sizing: border-box;
  display: block;
  position: absolute;
  left: -100000px;
  margin: -1px 0;
  width: 100%;
  z-index: 1051;
  border: 1px solid $fontColor; }

.select2-results {
  display: block; }

.select2-results__options {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start; }

.select2-results__option {
  padding: 10px 20px;
  font-weight: 400;
  color: $fontColor;
  font-family: $ago;
  font-size: 24px;
  line-height: 1.2;
  width: 100%;
  user-select: none;
  -webkit-user-select: none;
  text-transform: uppercase;
  transition: all $transitionSpeed $ease;

  &[aria-selected] {
    cursor: pointer; } }

.select2-container--open {
  .select2-dropdown {
    left: 0; } }

//   .select2-dropdown--above
//     border-bottom: none
//     border-bottom-left-radius: 0
//     border-bottom-right-radius: 0

//   .select2-dropdown--below
//     border-top: none
//     border-top-left-radius: 0
//     border-top-right-radius: 0

// .select2-container--below.select2-container .select2-selection--single
//   .select2-selection__rendered
//     border-bottom-left-radius: 0
//     border-bottom-right-radius: 0

.select2-search--dropdown {
  display: block;
  padding: 5px 15px;
  display: none; }

.select2-close-mask {
  border: 0;
  margin: 0;
  padding: 0;
  display: block;
  position: fixed;
  left: 0;
  top: 0;
  min-height: 100%;
  min-width: 100%;
  height: auto;
  width: auto;
  opacity: 0;
  z-index: 99;
  background-color: #fff;
  filter: alpha(opacity = 0); }

.select2-hidden-accessible {
  border: 0 !important;
  clip: rect(0 0 0 0) !important;
  -webkit-clip-path: inset(50%) !important;
  clip-path: inset(50%) !important;
  height: 1px !important;
  overflow: hidden !important;
  padding: 0 !important;
  position: absolute !important;
  width: 1px !important;
  white-space: nowrap !important; }

.select2-container--default {
  .select2-selection--single {

    .select2-selection__clear {
      cursor: pointer;
      float: right;
      font-weight: bold; }

    .select2-selection__placeholder {
      color: rgba($white, 0.5);
      font-weight: 400; }


    .select2-selection__arrow {
      display: block;
      position: absolute;
      right: 2px;
      top: 0;
      width: 16px;
      height: 100%;
      font-size: 0;
      color: transparent;

      &:after {
        content: '';
        position: absolute;
        top: 50%;
        right: 12px;
        transform: translateY(-50%);
        width: 28px;
        height: 28px;
        background: url(../img/dropdown-icon.svg) center center no-repeat;
        background-size: contain;
        transition: all $transitionSpeed $ease;
        transform-origin: center center; } } }

  &[dir="rtl"] .select2-selection--single {
    .select2-selection__clear {
      float: left; }

    .select2-selection__arrow {
      left: 1px;
      right: auto; } }

  &.select2-container--disabled .select2-selection--single {
    background-color: #eee;
    cursor: default;

    .select2-selection__clear {
      display: none; } }

  &.select2-container--open .select2-selection--single .select2-selection__arrow {
    &:after {
      transform: translateY(-50%) rotate(180deg); } }

  &.select2-container--open .select2-selection--multiple .select2-selection__rendered {
    &:after {
      transform: translateY(-50%) rotate(180deg); } }

  .select2-selection--multiple {
    cursor: text;

    .select2-search__field {
      padding: 0;
      border: 0;

      &::placeholder {
        color: rgba($white, 0.5); } }

    .select2-selection__rendered {
      box-sizing: border-box;
      list-style: none;
      margin: 0;
      width: 100%;

      li {
        list-style: none; } }

    .select2-selection__clear {
      cursor: pointer;
      float: right;
      font-weight: bold;
      margin-top: 5px;
      margin-right: 10px;
      padding: 1px; }

    .select2-selection__choice {
      float: left;
      margin-right: 10px;
      font-size: 16px;
      line-height: normal;
      color: $white;
      font-weight: 700;

      &:after {
        content: ',';
        color: $white;
        font-weight: 700; }

      &:nth-last-child(1),
      &:nth-last-child(2) {
        &:after {
          display: none; } } }

    .select2-selection__choice__remove {
      color: #fff;
      cursor: pointer;
      display: inline-block;
      font-weight: 400;
      margin-right: 5px; } }

  &[dir="rtl"] .select2-selection--multiple {
    .select2-selection__choice, .select2-search--inline {
      float: right; }

    .select2-selection__choice {
      margin-left: 5px;
      margin-right: auto; }

    .select2-selection__choice__remove {
      margin-left: 2px;
      margin-right: auto; } }

  &.select2-container--focus .select2-selection--multiple {
    border: solid black 1px;
    outline: 0; }

  &.select2-container--disabled {
    .select2-selection--multiple {
      background-color: #eee;
      cursor: default; }

    .select2-selection__choice__remove {
      display: none; } }

  &.select2-container--open {
    &.select2-container--above {
      .select2-selection--single, .select2-selection--multiple {
        border-top-left-radius: 0;
        border-top-right-radius: 0; } }

    &.select2-container--below {
      .select2-selection--single, .select2-selection--multiple {
        border-bottom-left-radius: 0;
        border-bottom-right-radius: 0; } } }

  .select2-results > .select2-results__options {
    max-height: 180px;
    overflow-y: auto; }

  .select2-results__option {
    &[role=group] {
      padding: 0; }

    &[aria-disabled=true] {
      color: #999; }

    &[aria-selected=true] {
      background-color: #dcdcdc;
      color: #000; }

    .select2-results__option {
      padding-left: 1em;

      .select2-results__group {
        padding-left: 0; }

      .select2-results__option {
        margin-left: -1em;
        padding-left: 2em;

        .select2-results__option {
          margin-left: -2em;
          padding-left: 3em;

          .select2-results__option {
            margin-left: -3em;
            padding-left: 4em;

            .select2-results__option {
              margin-left: -4em;
              padding-left: 5em;

              .select2-results__option {
                margin-left: -5em;
                padding-left: 6em; } } } } } } }

  .select2-results__option--highlighted[aria-selected] {
    background-color: #f5f5f5;
    color: #000; }

  .select2-results__group {
    cursor: default;
    display: block;
    padding: 6px; } }

@include r($desktop) {
  .select2-container {

    .select2-selection--single {

      .select2-selection__rendered {
        font-size: 24px; } } } }

@include r($tablet) {
  .select2-container {

    .select2-selection--single {

      .select2-selection__rendered {
        font-size: 18px;
        padding-top: 8px;
        padding-bottom: 12px; } } }

  .select2-results__option {
    padding: 8px 10px;
    font-size: 18px;
    line-height: 1.2; } }

