.form {
  // padding: 150px 0 150px

  &__grid {
    display: flex;
    align-items: center;
    width: 100%; }

  &__left {
    width: 50%;
    text-align: left;

    h2 {
      padding-bottom: 0;
      border-bottom: none;
      font-size: 100px; }

    p {
      font-size: 14px;
      line-height: 1.5;
      font-weight: 300;
      margin-top: 30px;
      text-transform: uppercase;
      letter-spacing: 1px; }

    .large {
      font-weight: 400;
      font-size: 24px;
      margin: 0 0 20px;
      letter-spacing: 5px; } }

  // &__info-row
  //   display: flex
  //   padding-top: 40px
  //   justify-content: flex-end

  //   ul
  //     text-align: left
  //     padding-left: 35px

  //     p
  //       padding-left: 20px
  //       position: relative

  //       &:before
  //         content: ''
  //         position: absolute
  //         left: 0
  //         top: 0
  //         line-height: inherit
  //         color: inherit
  //         font-size: inherit

  //   p
  //     font-size: 14px
  //     text-transform: uppercase
  //     color: $default
  //     line-height: 1.3
  //     margin-top: 12px

  &__right {
    width: 50%;
    background: $white;
    padding-left: 50px;
    position: relative; }

  &__thank {
    position: absolute;
    bottom: calc(100% + 40px);
    right: 0;
    padding: 50px;
    background: #fff;
    box-shadow: 0 0 32px rgba(0,0,0,0.2);
    display: flex;
    align-items: center;

    display: none;

    &-image {
      font-size: 0;
      max-width: 90px;
      width: 50%;
      min-width: 50px;

      img {
        width: 100%;
        height: auto; } }

    &-info {
      padding: 0 40px;

      p {
        font-size: 14px;
        text-transform: uppercase;
        color: #252525;
        margin-top: 10px;
        font-weight: 300; }

      .large {
        font-size: 30px;
        font-family: $dewi;
        text-transform: uppercase;
        margin: 0;
        color: #000; } }

    &-close {
      width: 50px;
      min-width: 50px;
      height: 50px;
      position: relative;
      cursor: pointer;

      &:before,
      &:after {
        position: absolute;
        left: 50%;
        top: 50%;
        content: '';
        background: #080808;
        width: 100%;
        height: 1px;
        transform: translate(-50%, -50%) rotate(45deg); }

      &:after {
        transform: translate(-50%, -50%) rotate(-45deg); } } }

  &__container {
    max-width: 510px;
    width: 100%;
    margin-left: auto;

    .btn {
      margin-top: 40px; }

    p {
      font-size: 10px;
      color: $grey;
      max-width: 310px;
      text-transform: uppercase;
      margin-top: 40px;
      line-height: 1.4; } }

  // &__desc
  //   font-size: 30px
  //   text-transform: uppercase
