  //   margin-top: 50px

  //   p
  //     font-size: 10px
  //     padding-left: 35px
  //     font-weight: 300
  //     text-transform: uppercase
  //     letter-spacing: 0.5px
  //     line-height: 1.5
  //     max-width: 300px

  .input-container {
    margin-top: 55px;
    max-width: 510px;

    &:nth-child(1) {
      margin-top: 0; } } }

@include r($hd) {
  .form {
    h2 {
      font-size: 80px; } } }

@include r($wide) {
  .form {
    h2 {
      font-size: 60px; }

    &__thank {
      padding: 35px;

      &-image {
        min-width: 60px;
        max-width: 60px; }

      &-info {
        padding: 0 25px;

        p {
          font-size: 14px; }

        .large {
          font-size: 24px; } }

      &-close {
        width: 40px;
        min-width: 40px;
        height: 40px; } } } }

@include r($desktop) {
