    //   p
 } }    //     margin-top: 10px

@include r($tablet) {
  .form {
    // padding: 45px 0

    &__grid {
      flex-direction: column;
      align-items: flex-start;
      justify-content: flex-start; }

    &__left {
      width: 100%;
      text-align: left;

      h2 {
        font-size: 36px; }

      .large {
        font-size: 16px;
        margin: 0 0 15px;
        letter-spacing: 2px; }

      p {
        font-size: 12px;
        margin-top: 15px; } }

    &__container {
      width: 100%;
      max-width: 100%; }

    &__right {
      margin: 0;
      padding: 15px 0;
      width: 100%; }

    .input-container {
      max-width: 100%; }

    &__desc {
      font-size: 18px;
      letter-spacing: 1px; }

    &__row {
      margin-top: 25px;

      .btn {
        width: 100%; }

      p {
        margin-top: 10px; } }

    .input-container {
      margin-top: 40px;

      &:nth-child(1) {
        margin-top: 30px; } }

    &__info {
      margin-top: 25px;

      p {
        font-size: 18px; } }

    &__info-row {
      flex-direction: column;
      align-items: flex-start;
      justify-content: flex-start;
      padding-top: 15px;

      ul {
        text-align: left;
        padding-left: 0;

        p {
          padding-left: 20px;
          position: relative; } }

      p {
        margin-top: 10px;
        font-size: 14px; } }

    &__thank {
      padding: 20px;
      max-width: 100%;
      width: 100%;
      bottom: 96%;
      left: 50%;
      transform: translateX(-50%);
      right: auto;


      &-image {
        min-width: 40px;
        max-width: 40px; }

      &-info {
        padding: 0 10px 0 15px;

        p {
          font-size: 12px;
          margin-top: 8px; }

        .large {
          font-size: 16px; } }

      &-close {
        width: 24px;
        min-width: 24px;
        height: 24px;
        align-self: flex-start;
        margin-top: -10px;
        margin-left: auto;
        margin-right: -10px; } } } }
