@charset "UTF-8";
/* FOOTER BEGIN */
.footer {
  position: relative;
  z-index: 100;
  background: #ffffff;
  border-top: 1px solid #e5e5e5;
  margin-top: 150px;
  padding-bottom: 50px;
}

.footer .logotype img {
  width: 200px;
  height: auto;
}

.footer__top {
  padding: 40px 0 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.footer .socials {
  padding: 30px 0;
  justify-content: center;
}

.footer__bottom {
  display: flex;
  align-items: flex-end;
  justify-content: space-between;
  margin: 0 -10px;
  width: calc(100% + 20px);
}

.footer__bottom p, .footer__bottom a {
  font-size: 14px;
  line-height: 1.4;
  color: #707070;
  display: inline-block;
}

.footer__bottom a:hover {
  color: #000000;
}

.footer__el {
  padding: 0 10px;
}

.footer__el:nth-child(1), .footer__el:nth-child(3) {
  max-width: 250px;
}

.footer__el:nth-child(2) {
  text-align: center;
}

.footer__el:nth-child(3) {
  text-align: right;
}

@media only screen and (max-width: 1336px) {
  .footer {
    margin-top: 110px;
    padding-bottom: 40px;
  }
  .footer .logotype img {
    width: 180px;
  }
}

@media only screen and (max-width: 1124px) {
  .footer {
    margin-top: 90px;
    padding-bottom: 30px;
  }
  .footer__top {
    flex-wrap: wrap;
    padding: 30px 0 0;
  }
  .footer__top .logotype {
    order: -3;
  }
  .footer__top .header__phone {
    order: -2;
    margin-left: auto;
  }
  .footer__top .menu {
    order: 0;
    width: calc(100% + 20px);
    justify-content: space-between;
    margin: 0 -10px;
    padding-top: 30px;
  }
  .footer__top .menu a {
    margin: 0;
    padding: 10px;
  }
  .footer__bottom {
    flex-wrap: wrap;
  }
  .footer__el {
    padding: 0 10px;
  }
  .footer__el:nth-child(1), .footer__el:nth-child(3) {
    max-width: 250px;
  }
  .footer__el:nth-child(2) {
    text-align: center;
    order: -1;
    width: 100%;
    margin-bottom: 20px;
  }
  .footer__el:nth-child(3) {
    text-align: right;
  }
}

@media only screen and (max-width: 992px) {
  .footer {
    margin-top: 60px;
  }
}

@media only screen and (max-width: 760px) {
  .footer {
    margin-top: 35px;
    padding-bottom: 20px;
  }
  .footer .logotype img {
    width: 150px;
  }
  .footer .menu {
    padding-top: 15px;
    order: 0;
  }
  .footer__top {
    padding: 25px 0 0;
    flex-direction: column;
  }
  .footer .socials {
    padding-top: 20px;
  }
  .footer .socials {
    justify-content: center;
    margin: 0 -20px;
    width: calc(100% + 40px);
    padding: 15px 20px;
    border-bottom: 1px solid rgba(149, 149, 149, 0.4);
  }
  .footer .header__phone {
    display: inline-block;
    order: 3;
    margin: 0 -20px;
    width: calc(100% + 40px);
    padding: 19px 20px;
    border-bottom: 1px solid rgba(149, 149, 149, 0.4);
    border-top: 1px solid rgba(149, 149, 149, 0.4);
    text-align: center;
    margin-top: 15px;
  }
  .footer__bottom {
    padding-top: 20px;
  }
  .footer__el {
    padding: 10px;
    width: 100%;
    text-align: center;
  }
  .footer__el:nth-child(1), .footer__el:nth-child(3) {
    max-width: 100%;
  }
  .footer__el:nth-child(2) {
    text-align: center;
    order: 0;
    width: 100%;
    margin-bottom: 0;
  }
  .footer__el:nth-child(3) {
    text-align: center;
  }
}

/* FOOTER END */
/* COMPONENTS BEGIN */
.logotype {
  font-size: 0;
  display: inline-block;
  cursor: pointer;
}

.socials {
  display: flex;
  align-items: center;
  margin: 0 -10px;
}

.socials a {
  margin: 0 8px;
  padding: 0 5px;
  line-height: 0;
  color: #707070;
}

.socials a svg path, .socials a svg circle {
  transition: all 0.25s ease-in-out;
}

.socials a:hover svg path, .socials a:hover svg circle {
  fill: #000000;
}

.socials a.instagram svg {
  width: 24px;
  height: 24px;
}

.socials a.fb svg {
  width: 12px;
  height: 24px;
}

.socials a.vk svg {
  width: 31px;
  height: 20px;
}

.socials a.youtube svg {
  width: 28px;
  height: 21px;
}

.socials a.pinterest svg {
  width: 23px;
  height: 29px;
}

.socials a.telegram svg {
  width: 34px;
  height: 30px;
}

.socials a.whatsup svg {
  width: 36px;
  height: 36px;
}

.menu {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.menu a {
  font-size: 14px;
  color: #000000;
  text-transform: uppercase;
  font-weight: 400;
  margin: 0 25px;
}

.menu a:hover {
  color: #00b8e4;
}

.menu .phone {
  display: none;
}

section {
  padding-top: 170px;
}

section.no-padding-top {
  padding-top: 0;
}

.image {
  display: inline-block;
  width: 100%;
  padding-bottom: 100%;
  overflow: hidden;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}

.slider-controls {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 20px;
}

.slider-controls .swiper-pagination {
  position: static;
  min-width: 100px;
  text-align: center;
  width: auto;
  font-size: 20px;
  color: #000;
  letter-spacing: 2px;
}

.slider-arrow {
  width: 60px;
  height: 60px;
  border-radius: 6px;
  border: 1px solid #00b8e4;
  transition: all 0.25s ease-in-out;
  cursor: pointer;
  position: relative;
}

.slider-arrow svg {
  position: absolute;
  top: 50%;
  left: 55%;
  transform: translate(-50%, -50%);
  width: 36px;
  height: 36px;
}

.slider-arrow svg path {
  transition: all 0.25s ease-in-out;
}

.slider-arrow--prev svg {
  left: 45%;
  transform: translate(-50%, -50%) rotate(180deg);
}

.slider-arrow:hover {
  background: #00b8e4;
}

.slider-arrow:hover path {
  fill: #ffffff;
}

ul {
  margin: 0;
  padding: 0;
}

ul li {
  margin: 0;
  padding: 0;
}

ul p {
  padding-left: 24px;
  position: relative;
}

ul p:before {
  position: absolute;
  left: 0;
  top: 0;
  line-height: inherit;
  color: inherit;
  font-size: inherit;
  content: '';
}

ul p span {
  font-weight: 700;
}

ul.no-points p {
  padding-left: 0;
}

ul.no-points p:before {
  display: none;
}

@media only screen and (max-width: 1336px) {
  section {
    padding-top: 110px;
  }
  section.no-padding-top {
    padding-top: 0;
  }
  .menu a {
    margin: 0 15px;
  }
}

@media only screen and (max-width: 1124px) {
  section {
    padding-top: 90px;
  }
  section.no-padding-top {
    padding-top: 0;
  }
  .fix-header main {
    padding-top: 30px;
  }
}

@media only screen and (max-width: 992px) {
  .fix-header main {
    padding-top: 40px;
  }
  section {
    padding-top: 60px;
  }
  section.no-padding-top {
    padding-top: 0;
  }
}

@media only screen and (max-width: 760px) {
  .socials a {
    font-size: 22px;
  }
  section {
    padding-top: 35px;
  }
  section.no-padding-top {
    padding-top: 0;
  }
  .fix-header main {
    padding-top: 60px;
  }
  .menu {
    flex-direction: column;
    padding: 10px 0 0;
  }
  .menu a {
    padding: 12px 0;
    margin: 0;
    width: 100%;
    text-align: center;
    position: relative;
  }
  .menu a:after {
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    width: 15px;
    height: 1px;
    content: '';
    background: rgba(149, 149, 149, 0.4);
    top: 0;
  }
  .menu a:first-child:after {
    display: none;
  }
  .slider-arrow {
    width: 40px;
    height: 40px;
    border-radius: 6px;
  }
  .slider-arrow svg {
    width: 20px;
    height: 20px;
  }
  .slider-controls {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 15px;
  }
  .slider-controls .swiper-pagination {
    font-size: 14px;
    letter-spacing: 1px;
  }
}

/* COMPONENTS END */
