.hero {
  padding-top: 150px;
  max-height: 1080px;
  height: 98vh;
  position: relative;
  display: flex;
  align-items: flex-end;
}

.hero .small {
  font-size: 16px;
  color: #ffffff;
  text-transform: uppercase;
  letter-spacing: 4px;
}

.hero h1 {
  color: #ffffff;
  max-width: 1000px;
}

.hero__image {
  width: 100%;
  height: 85%;
  position: absolute;
  left: 0;
  top: 0;
  z-index: 1;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  overflow: hidden;
}

.hero__image:after {
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  content: '';
  height: 100%;
  background: linear-gradient(to bottom, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0) 10%, rgba(0, 0, 0, 0.7) 100%);
}

.hero__bottom {
  position: relative;
  z-index: 2;
  width: 100%;
}

.hero__gallery {
  padding-top: 60px;
  margin: 0 -20px;
  width: calc(100% + 40px);
}

.hero .swiper-slide {
  padding: 0 20px;
  width: 25%;
}

.hero__gallery-item {
  display: inline-block;
  width: 100%;
  font-size: 0;
  position: relative;
  transition: all 0.25s ease-in-out;
  box-shadow: 0px 3px 5px 0px rgba(0, 0, 0, 0.1);
  position: relative;
}

.hero__gallery-item:hover {
  box-shadow: 0px 20px 8px 0px rgba(0, 0, 0, 0.2);
}

.hero__gallery-item .sale {
  position: absolute;
  left: 0;
  top: 15px;
  z-index: 2;
  font-size: 12px;
  color: #000000;
  background: #00b8e4;
  padding: 5px 15px;
  font-weight: 700;
  font-family: "RF Dewi Expanded", sans-serif;
}

.hero__gallery-image .image {
  padding-bottom: 45%;
}

.hero__gallery-info {
  background: #fff;
  padding: 14px 30px;
  min-height: 87px;
  height: 87px;
}

.hero__gallery-info .title {
  font-size: 12px;
  font-family: "RF Dewi Expanded", sans-serif;
  line-height: 1.2;
  color: #000000;
  text-transform: uppercase;
}

.hero__gallery-info .about {
  font-size: 14px;
  color: #7d7d7d;
  margin-top: 10px;
  line-height: 1.2;
  overflow: hidden;
  display: block;
  -webkit-line-clamp: 2;
  display: box;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  text-overflow: ellipsis;
  white-space: normal;
}

