  top: 0;
  width: 50px;
  height: 100%;
  content: '';
  background: linear-gradient(to right, rgba(255, 255, 255, 0) 0%, white 90%, white 100%);
  z-index: 2;
}

.reviews .slider-controls {
  margin-top: 50px;
}

.reviews__item {
  max-width: 1100px;
  width: 100%;
  margin: 0 auto;
  padding: 0 50px;
  height: 100%;
}

.reviews__item p {
  font-size: 24px;
  color: #7d7d7d;
  line-height: 1.5;
  font-weight: 300;
}

.reviews .swiper-wrapper {
  align-items: stretch;
}

.reviews .swiper-slide {
  height: auto;
}

.reviews__author {
  text-align: center;
  padding-top: 50px;
  margin-top: auto;
}

.reviews__author .name {
  font-size: 16px;
  color: #000000;
  font-weight: 700;
  font-family: "RF Dewi Expanded", sans-serif;
  line-height: 1.2;
}

.reviews__author .about {
  margin-top: 10px;
  font-size: 14px;
  font-weight: 300;
}

@media only screen and (max-width: 992px) {
  .reviews__slider {
    margin: 0 -35px;
    width: calc(100% + 70px);
    padding-top: 0;
  }
  .reviews__slider:before {
    width: 35px;
  }
  .reviews__slider:after {
    width: 35px;
  }
  .reviews__item {
    padding: 0 35px;
  }
  .reviews__item p {
    font-size: 20px;
  }
  .reviews__author {
    padding-top: 30px;
  }
  .reviews .slider-controls {
    margin-top: 30px;
  }
}

@media only screen and (max-width: 760px) {
  .reviews__slider {
    margin: 0 -20px;
    width: calc(100% + 40px);
  }
  .reviews__slider:after {
    display: none;
  }
  .reviews__slider:before {
    display: none;
  }
  .reviews__item {
    padding: 0 20px;
  }
  .reviews__item p {
    font-size: 16px;
  }
  .reviews__author {
    padding-top: 20px;
  }
  .reviews__author .name {
    font-size: 14px;
  }
  .reviews__author .about {
    margin-top: 5px;
    font-size: 12px;
  }
  .reviews .slider-controls {
    margin-top: 25px;
  }
}

.vacancies {
  padding: 120px 0 90px;
  background: #f7f7f7;
}

.vacancies__items {
  max-width: 750px;
  width: 60%;
  padding-right: 30px;
}

.vacancies__item {
  padding: 40px 0 30px;
  width: 100%;
  border-bottom: 1px solid #e1e1e1;
}

.vacancies__item:last-child {
  border-bottom: none;
}

.vacancies__row {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  margin: 0 -10px;
  width: calc(100% + 20px);
  padding-bottom: 10px;
}

.vacancies__row p {
  padding: 0 10px;
}

.vacancies__row .title {
  font-size: 18px;
  font-weight: 700;
  color: #000000;
  font-family: "RF Dewi Expanded", sans-serif;
}

.vacancies__row .price {
  font-size: 14px;
  font-weight: 300;
  color: #000000;
  text-align: right;
}

.vacancies__row .price span {
  font-weight: 400;
  font-size: 18px;
}

.vacancies__point-icon {
  min-width: 16px;
  width: 16px;
  text-align: center;
  margin-right: 15px;
}

.vacancies__point-icon img {
  height: auto;
}

.vacancies__point {
  padding: 4px 0;
  display: flex;
  align-items: center;
}

.vacancies__point p {
  font-size: 14px;
  color: #7d7d7d;
  font-weight: 300;
}

.vacancies__grid {
  display: flex;
  align-items: flex-start;
}

.vacancies__about {
  max-width: 400px;
  width: 40%;
  padding-left: 25px;
  margin-left: auto;
  padding-top: 40px;
}

.vacancies__about .title {
  font-size: 24px;
  font-family: "RF Dewi Expanded", sans-serif;
  font-weight: 400;
  padding-bottom: 10px;
}

.vacancies__about ul li {
  padding-top: 25px;
}

.vacancies__about ul p {
  font-size: 20px;
  padding-left: 35px;
  color: #7d7d7d;
}

@media only screen and (max-width: 992px) {
  .vacancies {
    padding: 90px 0 60px;
    background: #f7f7f7;
  }
  .vacancies__grid {
    flex-direction: column;
  }
  .vacancies__items {
    max-width: 100%;
    width: 100%;
    padding-right: 0;
    margin-top: -30px;
  }
  .vacancies__item {
    padding: 30px 0 20px;
  }
  .vacancies__about {
    max-width: 100%;
    width: 100%;
    padding-left: 0;
    margin-left: 0;
    padding-top: 30px;
  }
  .vacancies__about .title {
    font-size: 20px;
  }
  .vacancies__about ul li {
    padding-top: 20px;
  }
  .vacancies__about ul p {
    font-size: 18px;
    padding-left: 30px;
  }
}

@media only screen and (max-width: 760px) {
  .vacancies {
    padding: 35px 0;
    background: #f7f7f7;
  }
  .vacancies__items {
    margin-top: -15px;
  }
  .vacancies__item {
    padding: 25px 0;
  }
  .vacancies__item:last-child {
    border-bottom: 1px solid #e1e1e1;
  }
  .vacancies__row {
    flex-direction: column;
    justify-content: flex-start;
    margin: 0;
    width: 100%;
    padding-bottom: 10px;
  }
  .vacancies__row p {
    padding: 5px 0;
  }
  .vacancies__row .title {
    font-size: 16px;
  }
  .vacancies__row .price {
    font-size: 12px;
  }
  .vacancies__row .price span {
    font-size: 16px;
  }
  .vacancies__about {
    padding-top: 20px;
  }
  .vacancies__about .title {
    font-size: 18px;
  }
  .vacancies__about ul li {
